import { Component } from 'react';
import Input from './Input';
import { Formik, Form } from 'formik';
import * as Yup from 'yup';

class Register extends Component {
  constructor(props) {
    super(props);

    this.regSchema = Yup.object().shape({
      email: Yup.string()
      .email(' email')
      .required(' email'),
      password: Yup.string()
      .min(8, '   8 ')
      .matches(/(?=.*[0-9])(?=.*[a-z])(?=.*[A-Z])[0-9a-zA-Z]{8,}/g, '      ,     ')
      .required(' '),
      confirmPassword: Yup.string()
      .oneOf([Yup.ref('password'), null], '  ')
      .min(8, '   8 ')
      .required(' ')
    });
  }

  handleSubmit = (values) => {
    const { email, password } = values;
    this.props.handleRegister(email, password);
  }

  render() {
    return (
      <section className="form">
        <h1 className="form__heading"></h1>
        <Formik initialValues={{
          email: '',
          password: '',
          confirmPassword: '',
        }}
        validationSchema={this.regSchema}
        onSubmit={this.handleSubmit}>
          {
            ({ values, errors, touched, handleChange }) => (
              <Form className="form__form" name="register" noValidate>
                <Input values={values.email} errors={errors.email} touched={touched.email} handleChange={handleChange} isError={this.props.isError} name="email" placeholder="E-mail" type="email" isPopup={false} />
                <Input values={values.password} errors={errors.password} touched={touched.password} handleChange={handleChange} isError={this.props.isError} name="password" placeholder="" type="password" isPopup={false} />
                <Input values={values.confirmPassword} errors={errors.confirmPassword} touched={touched.confirmPassword} handleChange={handleChange} isError={this.props.isError} name="confirmPassword" placeholder=" " type="password" isPopup={false} />
                <button className="form__button" type="submit"></button>
              </Form>
            )
          }
        </Formik>
        {this.props.children}
      </section>
    )
  }
}

export default Register;